</div>




<style>
header{
    width: 100%;
    display: flex;
    justify-content:space-between;
    align-items: center;  
    padding: 10px 0;
}
.logo{
    font-family: 'Jost',sans-serif;
    font-weight: 500;
    font-size: 1.2rem;
    cursor: default;
    background: linear-gradient(-45deg, #e9af9e, #e78caf, #4a6bda, #8cdeff);
    background-clip: text;
    -webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
    background-size: 300% 200%;
    animation: gradient 9s ease infinite; 
}
@keyframes gradient{
    0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 59%;
	}
	100% {
		background-position: 0% 50%;
	}
}



.nav-links li{
    display: inline-block;
    text-transform: uppercase;
    padding: 0 1rem;
}

li,a{
    font-size: .9rem;
    font-weight: 500;
    text-decoration: none;
}

a:hover{
    border-bottom: solid #f2f2f2 2px;
     
}

/* .contactbtn:hover {
    border-bottom: solid #f2f2f2 2px;
} */

.menubtn{
    display: none;
}
/* ------------MOBILE MENU--------- */
.menu-slide{
    height: 100vh;
    width: 0;
    position: fixed;
    z-index: 30;
    top: 0;
    right: 0;
    overflow-x: hidden;
    transition: all 0.4s ease 0;
}



/* ------------MEDIA QUERIES--------- */
@media (max-width:700px){
