        padding: 1rem 0;
    }

    .closebtn{
        position: absolute;
        top: 30px;
        right: 20px;
        font-size: 1.7rem;
    }
    .menubtn{
        display: inherit;
        font-size: 1.7rem;
        color: #f2f2f2;
        margin-bottom: -3px;
    }
}
</style>







